<template>
  <section class="main">
    <h1 class="intro">{{ $t("HOW_TO_UNLINK") }}</h1>
    <div class="row">
      <div class="content">
        <p class="g-text" v-html="$t('UNLINK_DESCRIPTION')"></p>
        <v-button
          size="lg"
          type="negative"
          :loading="isUnlinkingDevice"
          @click="unlinkDevice"
        >
          {{ $t('UNLINK_DEVICE_BUTTON_LABEL') }}
        </v-button>
      </div>
      <div class="sidebar">
        <h2 class="sidebar__title">{{ $t("UNLINK_YOUR_PC") }}</h2>
        <p class="g-text">
          {{ $t("IT_WILL_ALSO_BE_UNLINKED") }}
        </p>
        <button class="sidebar__button" @click="unlinkPc">{{ $t("UNLINK") }}</button>
      </div>
    </div>
  </section>
</template>

<script>
import { mapActions } from 'vuex';
import VButton from '@/components/Button';

export default {
  components: {
    VButton,
  },
  data() {
    return {
      isUnlinkingDevice: false,
    };
  },
  computed: {
    title() {
      return this.$t('HOW_TO_UNLINK');
    },
  },
  methods: {
    ...mapActions(['clearLinkedDeviceData']),
    async unlinkDevice() {
      if (this.isUnlinkingDevice) {
        return;
      }

      await this.$confirm(this.$t('UNLINK_DEVICE_CONFIRMATION'), { okText: this.$t('YES') });

      this.isUnlinkingDevice = true;
      try {
        await this.$api.notifyProfileRemoval();
        localStorage.removeItem('old_lt');
        this.unlinkPc();
      } finally {
        this.isUnlinkingDevice = false;
      }
    },
    unlinkPc() {
      this.clearLinkedDeviceData();
      this.$router.push('/');
    },
  },
};
</script>

